.loader {
  width: 16px;
  height: 16px;
  box-sizing: border-box;
  background: #f6caca;
  border-radius: 50%;
  margin: 15px auto;
  animation: shadowPulse 2s linear infinite;
  display: block;
  position: relative;
  box-shadow: -24px 0 #fff, 24px 0 #fff;
}

@keyframes shadowPulse {
  33% {
    background: #fff;
    box-shadow: -24px 0 #ff3d00, 24px 0 #fff;
  }

  66% {
    background: #ff3d00;
    box-shadow: -24px 0 #fff, 24px 0 #fff;
  }

  100% {
    background: #fff;
    box-shadow: -24px 0 #fff, 24px 0 #ff3d00;
  }
}

